.home {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    color: #333;
}

.hero {
    position: relative;
    background-color:#2d2929;
    overflow: hidden;
    color: #fff;
    text-align: center;
    height: 50vh; /* Ensures the hero section covers the full viewport height */
}

.hero-overlay {
    background: rgba(0, 0, 0, 0.7); /* Increased opacity for better visibility */
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; 
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
    padding: 20px; 
}

.hero-content {
    position: relative;
    z-index: 2;
    padding: 50px 20px; /* Adjusted padding */
    text-align: center; /* Center-align the text */
}

.welcome-text {
    font-size: 3rem; /* Reduced font size for better fit */
    margin-bottom: 20px; /* Added margin for spacing */
    animation: moveText 10s linear infinite;
    color: #fefefe; /* Bright yellow color for visibility */ /* Semi-transparent background */
    padding: 5px 10px; /* Padding for better readability */
    border-radius: 5px; /* Rounded corners */
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7); /* Text shadow for better visibility */
}

.hero-description {
    font-size: 1.2rem;
    margin: 10px 0 20px 0; /* Adjusted margin for better spacing */
}

.cta-button {
    background: linear-gradient(90deg, #007bff, #0056b3);
    color: #fff;
    padding: 12px 24px;
    text-decoration: none;
    font-size: 1.2rem;
    border-radius: 5px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: background 0.3s, transform 0.3s;
}

.cta-button:hover {
    background: linear-gradient(90deg, #0056b3, #003d7a);
    transform: scale(1.05);
}

.hero-images {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.hero-image {
    position: absolute;
    max-width: 100%;
    opacity: 0.3;
    animation: slideImage 15s linear infinite;
}

@keyframes moveText {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(100%); }
}

@keyframes slideImage {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(100%); }
}

.features {
    display: flex;
    justify-content: space-around;
    padding: 60px 20px;
}

.feature {
    background-color: #fff;
    padding: 30px;
    border-radius: 12px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 30%;
    transition: transform 0.3s;
}

.feature:hover {
    transform: translateY(-10px);
}

.feature h2 {
    color: #007bff;
}

.feature-button {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s;
}

.feature-button:hover {
    background-color: #0056b3;
}

.about {
    background-color: #f5f5f5;
    padding: 60px 20px;
    text-align: center;
}

.about-details {
    display: flex;
    justify-content: space-around;
    padding: 20px 0;
}

.about-item {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 30%;
    text-align: center;
    transition: transform 0.3s;
}

.about-item:hover {
    transform: translateY(-10px);
}

.about-item h3 {
    color: #007bff;
}

.about-button {
    background-color: #007bff;
    color: #fff;
    padding: 8px 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s;
}

.about-button:hover {
    background-color: #0056b3;
}

.testimonials {
    background-color: #e9ecef;
    padding: 60px 20px;
    text-align: center;
}

.testimonial {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 20px 0;
    font-style: italic;
}

.footer {
    background-color: #282c34;
    background-size: cover;
    color: #fff;
    text-align: center;
    padding: 30px;
    margin-top: auto; /* Pushes footer to the bottom of the page */
    width: 100%;
    margin-left: 0px; /* Ensures the footer spans the full width of the page */
}

.footer a {
    color: #61dafb;
    text-decoration: none;
}

.footer a:hover {
    text-decoration: underline;
}

/* Make sure the body and html take full height */
html, body {
    height: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
}

.main-content {
    flex-grow: 1; /* Ensures the content area grows to fill available space */
}

@media (max-width: 768px) {
    .features {
        flex-direction: column;
        align-items: center;
    }

    .feature {
        width: 80%;
        margin-bottom: 20px;
    }

    .about-details {
        flex-direction: column;
    }

    .about-item {
        width: 80%;
        margin-bottom: 20px;
    }
}
